import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

import { BomRoutingModule } from './bom-routing.module';
import { BomComponent } from './bom.component';
import { BomActionComponent } from './bom-action/bom-action.component';
import { BomListComponent } from './bom-list/bom-list.component';
import { SharedModule } from '../shared/';
import { BomService } from './shared/bom.service';

@NgModule({
  imports: [
    CommonModule,
    BomRoutingModule,
    SharedModule
  ],
  declarations: [BomComponent, BomActionComponent, BomListComponent],
  providers: [BomService]
})
export class BomModule { }



