import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ExamineService } from './shared/examine.service';
import * as _ from 'lodash';
import * as moment from 'moment';


@Component({
  selector: 'app-examine',
  templateUrl: './examine.component.html',
  styleUrls: ['./examine.component.css']
})
export class ExamineComponent implements OnInit {
  AcceptanceNo: string;
  AllSearchProduct: Array<any>;
  AllSearchManu: Array<any>;
  dataLists: Array<any>;
  dataList: any;
  acceptanceSerail: string;
  nextPage: number;
  previousPage: number;
  OrganizationName: string;
  unitLists: any;
  ALLSearchInvNam: any;

  constructor(
    private examineService: ExamineService,
    private router: Router
  ) { }

  ngOnInit() {
    this.examineService.getAcceptanceNo().subscribe(result => {
      this.AcceptanceNo = result;
    })
    //廠商 含 品名
    this.examineService.getAllSearchProduct().subscribe(result => {

      this.AllSearchProduct = result;
       console.log(result)
      var unitLists = [];
      _.map(result, O => {
        var a = _.findIndex(unitLists, { 'parameterValue': O.parameterValue });
        if (a < 0) {
           unitLists.push({ parameterValue: O.parameterValue,
            units: O.units })
        }
      })  
       this.unitLists = unitLists;
  

    })
    //全部廠商
    this.examineService.getAllSearchManu().subscribe(result => {
      this.AllSearchManu = result;
    })
    this.examineService.SearchInvNam().subscribe(result => {
      console.log(result)
      this.ALLSearchInvNam = result;
    })
     

    this.nextPage = 1;

  }

  //新增驗收單號
  //Acceptance驗收單號 object資料
  createAcceptance(Acceptance: any) {
    this.examineService.NewAcceptance(Acceptance).subscribe(result => {
      var dataList = JSON.parse(result["_body"]);
      if (result.ok) {
        if (this.dataLists === undefined) {
          this.dataLists = [];
        }
        this.dataLists.push(dataList);
      }
    })
  }

  //換頁
  changePage(goPage: number) {
    this.nextPage = goPage;
  }
  //紀錄-驗收單號含批號 & 產品名稱
  examine(acceptanceSerail: number) {
    this.dataList = _.find(this.dataLists, { acceptanceSerail: acceptanceSerail });

  }

  changeDataList(dataList: any) {
    var a = _.findIndex(this.dataLists, {
      acceptanceNo: dataList.acceptanceNo,
      acceptanceSerail: dataList.acceptanceSerail
    });
    this.dataLists[a] = dataList;
  }


  changeDataListValue(dataList) {
    this.dataList = dataList;
    this.nextPage = 3;
  }

  delDataList(data) {
    var dataLists = this.dataLists.filter((dataList) => dataList.acceptanceSerail != data.acceptanceSerail);
    this.dataLists = dataLists;
  }

  doNextPage(currentPage, previousPage) {
    this.nextPage = currentPage;
    this.previousPage = previousPage;
  }

  send() {
    this.examineService.addBatch_Omnipotent(this.dataLists).subscribe(result => {
      if (result.ok) {
        this.router.navigate(['/menu'])
      }
    })
  }
}



