import { Component, OnInit, Input, Output, EventEmitter, ViewChild } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import * as _ from 'lodash';
import * as moment from 'moment';

@Component({
  selector: 'app-inventory-track',
  templateUrl: './inventory-track.component.html',
  styleUrls: ['./inventory-track.component.css']
})
export class InventoryTrackComponent implements OnInit {
  @Input() InInventoryNo: any;
  @Input() dataLists: any;
  createBy: any;
  createTime: any;
  constructor() { }

  ngOnInit() {
    this.dataLists = _.filter(this.dataLists, O => O.status);
    this.createBy = this.dataLists[0].createBy;
    this.createTime = this.dataLists[0].createTime;
  }

}



