import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { ExamineComponent } from './examine.component';
import { ExamineRoutingModule } from './examine-routing.module';
import { ExamineAllComponent } from './examine-all/examine-all.component';
import { ExamineDoComponent } from './examine-do/examine-do.component';
import { ExaminePrintComponent } from './examine-print/examine-print.component';
import { ExamineAllActionComponent } from './examine-all/examine-all-action/examine-all-action.component';
import { ExamineAllListComponent } from './examine-all/examine-all-list/examine-all-list.component';
import { BsDropdownModule, ModalModule, PaginationModule, DatepickerModule, TabsModule } from 'ngx-bootstrap';
import { SharedModule } from '../shared/shared.module';
import { ExamineService } from './shared/examine.service';

import { FilterManufacturerNamePipe } from './shared/filter-manufacturerName.pipe';
import { FilterProductNamePipe } from './shared/filter-productName.pipe';
import { FilterParameterValue } from './shared/filter-parameterValue.pipe';

@NgModule({
  imports: [
    CommonModule,
    ExamineRoutingModule,
    BsDropdownModule.forRoot(),
    SharedModule
  ],
  declarations: [ExamineComponent,
                 ExamineAllComponent,
                 ExamineDoComponent, 
                 ExaminePrintComponent, 
                 ExamineAllActionComponent,
                 ExamineAllListComponent,
                 FilterManufacturerNamePipe,
                 FilterProductNamePipe,
                 FilterParameterValue],
  providers: [ExamineService]
})
export class ExamineModule { }




