import { Component, OnInit } from '@angular/core';
import { BomService } from './shared/bom.service';
import { Router } from '@angular/router';
import * as _ from 'lodash';
import * as moment from 'moment';
declare var jquery: any;
declare let $: any;

@Component({
  selector: 'app-bom',
  templateUrl: './bom.component.html',
  styleUrls: ['./bom.component.css']
})
export class BomComponent implements OnInit {
  currentPage: number
  dataList: any;
  dataByFinish: any;
  constructor(
    private bomService: BomService,
    private router: Router
  ) { }

  ngOnInit() {
    this.currentPage = 1;
    this.dataList = _.assign({}, this.dataList, {
      MakeUpTime: moment(new Date()).format("YYYY/MM/DD")
    })
  }

  next(dataList) {
    this.dataList = dataList
    this.currentPage = 2;
  }

  finish() {
    this.bomService.addNewBillOfMatrials(this.dataByFinish).subscribe(result => {
      if (result.ok) {
       
      }
    })
  }

  nextToFinish(dataLists) {
    this.dataByFinish = { BillO: this.dataList, Products: dataLists }

  }

  back(page) {
    $('#creation').modal('hide');
    var x = this
    $('#creation').on('hidden.bs.modal', function (e) {
      switch (page) {
        case 'create': {
           x.test();
          break;
        }
        case 'menu': {
          x.router.navigate(['menu']);
          break;
        }
      }
    })
  }
  test(){
   this.dataList = _.assign({}, this.dataList, {
    ProductName: null,
    MakeUpQuantity: null,
    MakeUpUnit: null,
    MakeUpProductType: null,
    useingQty: null,
    Serailkey: null,
    Unit: null,
    MakeUpTime: moment(new Date()).format("YYYY/MM/DD")
  })
    this.currentPage = 1
  }
}



