import { Pipe, PipeTransform } from '@angular/core';
import * as _ from 'lodash';
import { ExamineService } from '../shared/examine.service';

@Pipe({
    name: 'filterParameterValue'
})

export class FilterParameterValue implements PipeTransform {

    constructor(
        private examineService: ExamineService
    ) { }

    transform(dataLists: Array<any>, ProductCode: string): any {
        return  _.find(dataLists, { productCode: ProductCode }).parameterValue;
    }
}


