import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

import { InventoryRoutingModule } from './inventory-routing.module';
import { InventoryListComponent } from './inventory-list/inventory-list.component';
 
import { InventoryQrcodeComponent } from './inventory-qrcode/inventory-qrcode.component';
import { InventoryService } from './shared/inventory.service';
import { InventoryTrackComponent } from './inventory-track/inventory-track.component';
import { SharedModule } from '../shared/shared.module';

@NgModule({
  imports: [
    CommonModule,
    InventoryRoutingModule,
    SharedModule
  ],
  declarations: [InventoryListComponent , InventoryQrcodeComponent, InventoryTrackComponent],
  exports: [
    InventoryListComponent , InventoryQrcodeComponent, InventoryTrackComponent
  ],
  providers: [InventoryService]
})
export class InventoryModule { }



