import { Pipe, PipeTransform } from '@angular/core';
import * as _ from 'lodash';

@Pipe({
    name: 'filterManufacturerName'
})

export class FilterManufacturerNamePipe implements PipeTransform {

    constructor(
    ) { }

    transform(dataLists: Array<any>, ManufacturerID: number): any {
        return  _.find(dataLists, { id: ManufacturerID }).manufacturerName;
    }
}


