import { Component, OnInit, Input, Output, OnChanges, EventEmitter, ViewChild, TemplateRef, ViewContainerRef, } from '@angular/core';
import * as _ from 'lodash';
import * as moment from 'moment';
import { Router } from '@angular/router';
import { ExamineService } from '../shared/examine.service'
declare var jquery: any;
declare let $: any;
declare var demo_h5_upload_ops: any;
declare var image_data: any;

@Component({
  selector: 'app-examine-do',
  templateUrl: './examine-do.component.html',
  styleUrls: ['./examine-do.component.css']
})
export class ExamineDoComponent implements OnInit, OnChanges {
  @Input() dataList: any;
  @Input() ALLSearchInvNam: any;
  @ViewChild('templateRef') templateRef: TemplateRef<any>;
  @ViewChild('img') img: any;
  @Output() changePage = new EventEmitter();
  @Output() changeDataList = new EventEmitter();
  data: any = {};
  AcceptanceItemsData: Array<any>;
  dialogRef: any;
  banStyle: boolean = false;
  status: number;
  falseNum: number;
  enoughQuantity: any;
  lackQuantity: any;
  num: number;
  InvCodes: Array<any> = [{ InvCode: 'AKU01', InvName: '乾倉' },
  { InvCode: 'AKU02', InvName: '食材前處理區' }]
  constructor(
    private examineService: ExamineService,
    private router: Router,
    vcRef: ViewContainerRef,
  ) {


  }
  ngOnChanges(changes: any) {

  }
  mychange(enoughQuantity) {
    this.enoughQuantity = enoughQuantity;
    this.lackQuantity = this.dataList.acceptanceQuantity- enoughQuantity;
    var over = enoughQuantity / this.dataList.batchQuantity;
    if (over > Math.floor(over)) {
      this.num = (Math.floor(over) + 1)
    } else {
      this.num = (Math.floor(over))
    }
    this.test(this.dataList, this.num)
  }
  ngOnInit() {
    this.loadScript('../assets/js/test.js');
    this.AcceptanceItemsData = [];
    demo_h5_upload_ops.init();
  }

  loadScript(src) {
    var script = document.createElement("script");
    script.type = "text/javascript";
    document.getElementsByTagName("body")[0].appendChild(script);
    script.src = src;
  }

  ////Button Style
  getBackgroundO(value) {
    var a = _.findIndex(this.AcceptanceItemsData, { 'item': value });
    if (a >= 0) {
      if (this.AcceptanceItemsData[a].name === true) {
        return "url('../../assets/img/btn/OGreen.svg')"
      } else {
        return "url('../../assets/img/btn/OG.svg')"
      }
    }

  }

  getBackgroundX(value) {
    var a = _.findIndex(this.AcceptanceItemsData, { 'item': value });
    if (a >= 0) {
      if (this.AcceptanceItemsData[a].name === false) {
        return "url('../../assets/img/btn/XR.svg')"
      } else {
        return "url('../../assets/img/btn/XG.svg')"
      }
    }

  }


  ////Button function
  O(type, name, value) {


    if (_.findIndex(this.AcceptanceItemsData, function (O) { return O.item == type }) < 0) {
      this.AcceptanceItemsData.push({
        'item': type,
        'name': name,
        'value': value
      })
    } else {
      var a = _.findIndex(this.AcceptanceItemsData, { 'item': type });
      this.AcceptanceItemsData[a].name = name
    }

    // this.test(type, value)

  }


  ///ban Button 
  ban() {
    var filter = _.filter(this.AcceptanceItemsData, function (o) {
      return o.name == false;
    })
    this.falseNum = filter.length
    if (filter.length > 0) {
      return true
    }
  }



  select(InvCode, InvName) {
    this.data = _.assign({}, this.data, {
      InvCode: InvCode,
      InvName: InvName
    });
  }


  push(status) {
    /*   this.modalService.open(this.templateRef, { isBlocking: false, dialogClass: 'modal-dialog' }).then(dialog => {
        this.dialogRef = dialog;
      }); */
    var itemTotal = 0;
    var newStatus = 0;
    _.map(this.AcceptanceItemsData, (O) => {
      if (O.name) {
        itemTotal = itemTotal + O.value
      }
    })

     
    this.dataList = _.assign({}, this.dataList, {
      acceptanceItems: itemTotal,
      acceptancePicture: this.img.nativeElement.currentSrc,
      acceptanceResult: true,
      acceptQuantity: parseInt(this.enoughQuantity),
      invCode: this.data.InvCode,
      status: status ? 1 : 2,
      enoughQuantity: this.enoughQuantity,
      lackQuantity: this.lackQuantity
    });

    if ((this.AcceptanceItemsData.length == 8) && this.data.InvCode && this.dataList.enoughQuantity) {
      this.banStyle = true;
      this.examineService.AcceptanceOfProduct(this.dataList).subscribe(result => {

        if (result.ok) {
          this.changeDataList.emit(this.dataList)
          if(!status) {
            this.changePage.emit(1);
            } 
        }
        // 更改狀態(次單號, 狀態)
      });
    }
      
  }



  close() {
    $(function () {
      $('#examine').modal('toggle');
    });
  }
  back(page) {
    $('#examine').modal('hide');
    var x = this
    $('#examine').on('hidden.bs.modal', function (e) {
      switch (page) {
        case 'examineAll': {
          x.changePage.emit(1);
          break;
        }
        case 'menu': {
          x.router.navigate(['menu']);
          break;
        }
      }
    })
  }


  acceptanceSerail(acceptanceSerail) {
    if (acceptanceSerail < 9) {
      return "0" + acceptanceSerail
    } else {
      return acceptanceSerail
    }
  }

  test(dataList, num?) {
    var value = dataList.productCode + this.dataList.acceptanceNo.substring(1, 9);

    this.examineService.getSubBatchNumber(value).subscribe(result => {
      var startNum: string = result.substring(13);
      
      if (num == 0) {
        var endNum: string = (parseInt(result.substring(13)) + (num)).toString();
      } else {
        var endNum: string = (parseInt(result.substring(13)) + (num - 1)).toString();
      }

      this.dataList = _.assign({}, this.dataList, {
        BatchNumberFirst: value + '-' + startNum,
        BatchNumberLast: value + '-' + endNum
      });
    })
  }
}



