import { Component, OnInit, Input, Output, OnChanges, EventEmitter, ViewChild } from '@angular/core';
import * as _ from 'lodash';
import * as moment from 'moment';
 
@Component({
  selector: 'app-examine-all-action',
  templateUrl: './examine-all-action.component.html',
  styleUrls: ['./examine-all-action.component.css']
})
export class ExamineAllActionComponent implements OnInit {
  @Input() AcceptanceNo: any;
  @Input() AllSearchProduct: any;
  @Input() AllSearchManu: Array<any>;
  @Input() unitLists: Array<any>;
  @Output() create = new EventEmitter();
  @ViewChild('date') date: any;
  test: any;
  data: any;
  NewAllSearchProduct: any;
  parameterValueByUnits: any;
   
  units: Array<any> = [{ id: 1, name: '盒' },
  { id: 2, name: '包' },
  { id: 3, name: '箱' },
  { id: 4, name: '罐' }]
  unitName = {
    斤: 4
  };
  constructor() {
    
  }

  ngOnChanges(changes: any) {
      }

  ngOnInit() {
    this.loadScript('../assets/js/theme.js')
    this.data = {
    }
  }
 
  select(type, value ) {
   
    this.data = _.assign({}, this.data, {
      [type]: value
    });
      
    if(type === 'ManufacturerName') {
      this.data.ProductName = null;
     this.NewAllSearchProduct = _.filter(this.AllSearchProduct,O => O.manufacturerName === value);
    }

    if(type === 'unit') {
     this.parameterValueByUnits = _.filter(this.AllSearchProduct,O => O.parameterValue === value)[0].units;
    }
  }

  loadScript(src) {
    var script = document.createElement("script");
    script.type = "text/javascript";
    document.getElementsByTagName("body")[0].appendChild(script);
    script.src = src;
  }
  
  //新增單筆次單號
  add() {
    this.data = _.assign({}, this.data, {
      parameterValue: this.data['unit']
    });
    
    this.data['unit'] = this.parameterValueByUnits;
    this.data['exprieTime'] = this.date.nativeElement.innerHTML;
    this.data['acceptanceNo'] = this.AcceptanceNo;
    this.data['acceptQuantity'] = 0;
    this.date.nativeElement.innerHTML = ""
    this.create.emit(this.data);
    this.data = {};
  }

}



