import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

import { StockoutRoutingModule } from './stockout-routing.module';
import { StockoutAllComponent } from './stockout-all/stockout-all.component';
import { StockoutPrintComponent } from './stockout-print/stockout-print.component';
import { StockoutComponent } from './stockout.component';

import { StockoutService } from './shared/stockout.service';
import { SharedModule } from '../shared/';

@NgModule({
  imports: [
    CommonModule,
    StockoutRoutingModule,
    SharedModule
  ],
  declarations: [StockoutAllComponent, StockoutPrintComponent, StockoutComponent],
  providers: [StockoutService]
})
export class StockoutModule { }



