import { Component, OnInit, Input, Output, EventEmitter, ViewChild } from '@angular/core';
import { ExamineService } from '../../shared/examine.service';

@Component({
  selector: 'app-examine-all-list',
  templateUrl: './examine-all-list.component.html',
  styleUrls: ['./examine-all-list.component.css']
})
export class ExamineAllListComponent implements OnInit {
  @Input() dataLists: any;
  @Input() AllSearchManu: Array<any>;
  @Input() AllSearchProduct: Array<any>;
  @Output() doChangePage = new EventEmitter();
  @Output() doChangeDataListValue = new EventEmitter();
  @Output() doDelDataList = new EventEmitter();
  @Output() doExamine = new EventEmitter();
  constructor(
    private examineService: ExamineService
  ) { }

  ngOnInit() {
  
  }

  //回傳該筆次單號
  //acceptanceSerail次單號
  examine(acceptanceSerail) {
    this.doChangePage.emit(2);
    this.doExamine.emit(acceptanceSerail);
  }

  acceptanceSerail(acceptanceSerail) {
    if (acceptanceSerail < 9) {
      return "0" + acceptanceSerail
    } else {
      return acceptanceSerail
    }
  }

  log(dataList, status){
   if(status == 1){
   
    this.doChangeDataListValue.emit(dataList)
   }
    
  }

  del(acceptanceNo, acceptanceSerail) {
    event.stopPropagation();
    var data ={ acceptanceNo: acceptanceNo, acceptanceSerail: acceptanceSerail }
    this.examineService.RemoveAcceptanceProduct(data).subscribe(result => {
      if(result.ok) {
        this.doDelDataList.emit(data)  
      }

    })

     
  }
}



