import { Injectable } from '@angular/core';
import { Http, Response, Headers, RequestOptions } from "@angular/http";
import 'rxjs/add/operator/map';

@Injectable()
export class ExamineService {
  url = '/YCYF/NewAcceptance';
  url2 = '/YCYF/AddAcceptanceProduct'
  url3 = '/YCYF/GetAcceptance?acceptanceNo='
  url4 = '/YCYF/AcceptanceOfProduct'

  url5 = 'YCYF/AllSearchManu';
  url6 = 'YCYF/AllSearchManufacturers';
  url7 = 'YCYF/RemoveAcceptanceProduct';
  url8 = 'YCYF/getSubBatchNumber?BatchNumber='
  url9 = 'YCYF/GetOneAcceptance?';
  url10 = 'YCYF/addBatch_Omnipotent';
  url11 = 'YCYF/GetOneAcceptance?'
  url12 = 'YCYF/SearchInvName'
  constructor(
    private http: Http
  ) { }


  getAcceptanceNo() {
    return this.http.get(this.url)
      .map((response: Response) => response.json());
  }

  NewAcceptance(data) {
    return this.http.post(this.url2, data)
      .map((response: Response) => response);
  }

  GetAcceptance(data) {
    return this.http.get(this.url3 + data)
      .map((response: Response) => response.json());
  }

  AcceptanceOfProduct(data) {
    return this.http.post(this.url4, data)
      .map((response: Response) => response);
  }


  getAllSearchProduct() {
    return this.http.get(this.url5)
      .map((response: Response) => response.json());
  }

  getAllSearchManu() {
    return this.http.get(this.url6)
      .map((response: Response) => response.json());
  }

  RemoveAcceptanceProduct(data) {
    return this.http.post(this.url7, data)
      .map((response: Response) => response);
  }

  getSubBatchNumber(data) {
    return this.http.get(this.url8 + data)
      .map((response: Response) => response.json());
  }

  GetOneAcceptance(a, b) {
    var option = "AcceptanceNo=" + a + "&" + "AcceptanceSerail=" + b
    return this.http.get(this.url11 + option)
      .map((response: Response) => response.json());
  }

  addBatch_Omnipotent(dataArray) {
    return this.http.post(this.url10, dataArray)
      .map((response: Response) => response);
  }

  SearchInvNam() {
    return this.http.get(this.url12)
    .map((response: Response) => response.json());
  }

}





