import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { MenuComponent } from './menu.component';
import { HomeComponent } from './home/home.component';

import { ExamineAllComponent } from '../examine/examine-all/examine-all.component';
import { ExamineComponent } from '../examine/examine.component';
import { ExamineDoComponent } from '../examine/examine-do/examine-do.component';
import { ExaminePrintComponent } from '../examine/examine-print/examine-print.component';

import { StockoutComponent } from '../stockout/stockout.component';
import { StockoutAllComponent } from '../stockout/stockout-all/stockout-all.component';
import { StockoutPrintComponent } from '../stockout/stockout-print/stockout-print.component';

import { InventoryQrcodeComponent } from '../inventory/inventory-qrcode/inventory-qrcode.component';
import { InventoryListComponent } from '../inventory/inventory-list/inventory-list.component';

import { BomComponent } from '../bom/bom.component';
import { TrackComponent } from '../track/track.component';
import { ExamineQrcodeComponent } from '../examine-qrcode/examine-qrcode.component';
import { AuthguardGuard } from '../shared/guard/authguard.guard';

const routes: Routes = [
    {
        path: 'menu',
        component: MenuComponent,
        canActivate: [AuthguardGuard],
        children: [
            {
                path: '',
                component: HomeComponent
            },
            {
                path: 'examine',
                component: ExamineComponent
            },
            {
                path: 'stockOut',
                component: StockoutComponent
            },
            {
                path: 'inventoryQrcode',
                component: InventoryQrcodeComponent
            },
            {
                path: 'inventoryList/:id',
                component: InventoryListComponent
            },
            {
                path: 'bom',
                component: BomComponent
            },
            {
                path: 'track',
                component: TrackComponent
            },
        ]
    },{
        path: 'examineQrcode/:acceptanceNo',
        component: ExamineQrcodeComponent,
    }

];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})
export class MenuRoutingModule { }



