import { Component, OnInit, Input, Output, EventEmitter, ViewChild } from '@angular/core';
import * as _ from 'lodash';
import * as moment from 'moment';

@Component({
  selector: 'app-bom-action',
  templateUrl: './bom-action.component.html',
  styleUrls: ['./bom-action.component.css']
})
export class BomActionComponent implements OnInit {
 
  @Input() dataList: any;
  @Output() next = new EventEmitter();
  @ViewChild('date') date: any;
  constructor() { }

  ngOnInit() {
    this.loadScript('../../assets/js/theme.js');
     
    

  }

  loadScript(src) {
    var script = document.createElement("script");
    script.type = "text/javascript";
    document.getElementsByTagName("body")[0].appendChild(script);
    script.src = src;
  }

  send() {
    var dataList = _.assign({}, this.dataList, {
      ExprieTime: this.date.nativeElement.innerHTML
    })
    this.next.emit(dataList);
  }
}



