import { Component, OnInit, OnDestroy } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { InventoryService } from '../shared/inventory.service';
import * as _ from 'lodash';
import * as moment from 'moment';
import { Router } from '@angular/router';
declare var jquery: any;
declare let $: any;

@Component({
  selector: 'app-inventory-list',
  templateUrl: './inventory-list.component.html',
  styleUrls: ['./inventory-list.component.css']
})
export class InventoryListComponent implements OnInit {
  InInventoryNo: any;
  dataLists: any;
  AcceptanceItemsData: Array<any>;
  createBy: any;
  createTime: any;
  currentPage: number
  sub: any;
  NO: any;
  constructor(
    private route: ActivatedRoute,
    private inventoryService: InventoryService,
    private router: Router,
  ) { }

  ngOnInit() {
    this.currentPage = 1;
    this.sub = this.route.params.subscribe(params => {
      let id = params['id'];
      this.inventoryService.InInventoryNo = id
      if (id) {
        this.InInventoryNo = id;
        this.inventoryService.NewInInventory(id).subscribe(result => {
          this.NO = result.inInventoryNo;
          this.dataLists = result.outInventorysData;
          this.createBy = this.dataLists[0].createBy;
          this.createTime = this.dataLists[0].createTime;
          this
        })
      }
    });

    this.AcceptanceItemsData = [];
  }


  ////Button Style
  getBackgroundO(value) {

    if (this.dataLists[value].status === true) {
      return "url('../../assets/img/btn/OGreen.svg')"
    } else {
      return "url('../../assets/img/btn/OG.svg')"
    }
  }

  getBackgroundX(value) {
    if (this.dataLists[value].status === false) {
      return "url('../../assets/img/btn/XR.svg')"
    } else {
      return "url('../../assets/img/btn/XG.svg')"
    }
  }

  ////Button function
  O(type, name) {
    this.dataLists[type].status = name
  }





  //清單狀態每筆都有選擇，顯示點貨確認按鈕
  //否則只顯示繼續掃描
  status() {
    var type = false;
    var statusArray = [];
    if (this.dataLists) {
      var dataListsLength = this.dataLists.length;
      for (var x = 0; x < dataListsLength; x++) {
        statusArray.push({ status: this.dataLists[x].status })
      }
      var L = _.filter(statusArray, O => {
        if (O.status === null) {
          return O
        }
      }).length

      var LOK = _.filter(this.dataLists, O => {
        if (O.status == true) {
          return O
        }
      }).length

      if ((L === 0) && LOK > 0) {
        type = true;
      }
      return type
    }
  }
  /*   status() {
      var type = false;
      var statusArray = [];
      if (this.dataLists) {
        var dataListsLength = this.dataLists.length;
        for (var x = 0; x < dataListsLength; x++) {
          statusArray.push({ status: this.dataLists[x].status })
        }
        var L = _.filter(statusArray, O => {
          if (O.status === null) {
            return O
          }
        }).length
        console.log(L)
        if (L === 0) {
          type = true;
        }
        return type
      }
    } */

  send() {
    this.currentPage = 2;
  }

  finishSend() {
    var finishData = _.filter(this.dataLists, O => O.status)
    /* var finishData = _.assign({},  _.filter(this.dataLists, O => O.status), {
      InInventoryNo: this.InInventoryNo
    }); */
    var L = _.filter(this.dataLists, O => O.status).length;
  //this.InInventoryNo
    for (var x = 0; x < L; x++) {
      finishData[x].InInventoryNo = this.NO
    }
    console.log("-----------"+JSON.stringify(finishData))
    this.inventoryService.AddInInventorysDetails(finishData).subscribe(result => {
      if (result.ok) {
        $('#stockIn').modal('show');
      }
    })

  }

  back() {
    $('#stockIn').modal('hide');
    var x = this
    $('#stockIn').on('hidden.bs.modal', function (e) {
      x.router.navigate(['menu']);
    })
  }

}


