import { Injectable } from '@angular/core';
import { Http, Response, Headers, RequestOptions } from "@angular/http";
import 'rxjs/add/operator/map';

@Injectable()
export class AuthService {
  getPostsURI = '/authentication/Authenticate';
  LoginActionFilterUrl= "YCYF/LoginActionFilter";
  LogoutUrl= '/authentication/Login'
  isUserLoggedIn: any;
  userId: any;
  constructor(private http: Http
  ) {
    this.isUserLoggedIn = false;
   }


  login(user) {
    return this.http.post(this.getPostsURI, user)
      .map((response: Response) => response.json());
  }

  /* login() {
    return this.http.get('/api/values')
    .map((response: Response) => response.json());
  } */

 //重導時判斷 登入資料
 LoginActionFilter() {
  return this.http.get(this.LoginActionFilterUrl)
  .map((response: Response) => response.json());
 }

 Logout() {
  return this.http.get(this.LogoutUrl)
  .map((response: Response) => response.json());
 }
 
}



