import { Injectable } from '@angular/core';
import { Http, Response, Headers, RequestOptions } from "@angular/http";
import 'rxjs/add/operator/map';

@Injectable()
export class StockoutService {
  url = '/YCYF/NewOutInventory';
  url2 = '/YCYF/GetOutInventory';
  url5 = 'YCYF/AllSearchManu';
  url6 = 'YCYF/addOutInventory';
  url7 = 'YCYF/AllSearchOrganization'
  constructor(
    private http: Http
  ) { }

  //取得新出庫單號
  NewOutInventory() {
    return this.http.get(this.url)
      .map((response: Response) => response.json());
  }

  //取得要新增的出庫產品資料
  GetOutInventory(data) {
    return this.http.post(this.url2, data)
    .map((response: Response) => response);
  }

  getAllSearchProduct() {
    return this.http.get(this.url5)
      .map((response: Response) => response.json());
  }

  addOutInventory(data){
    return this.http.post(this.url6, data)
    .map((response: Response) => response);
  }

  AllSearchOrganization() {
    return this.http.get(this.url7)
    .map((response: Response) => response.json());
  }
  

}



