import { Component, OnInit, Input, Output, EventEmitter, ViewChild } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import * as _ from 'lodash';
import * as moment from 'moment';
import { BomService } from '../shared/bom.service';

@Component({
  selector: 'app-bom-list',
  templateUrl: './bom-list.component.html',
  styleUrls: ['./bom-list.component.css']
})
export class BomListComponent implements OnInit {
  @Input() dataList: any;
  @Output() nextToFinish = new EventEmitter();
  dataLists: Array<any>;
  unitName = {
    斤: 4
  };
  constructor(
    private bomService: BomService
  ) { }

  ngOnInit() {
    this.dataLists = [];
    
  }
  send() {
     
    var newDataList = _.assign({}, this.dataList, {
      Unit: this.unitName[this.dataList['Unit']]
    });
    
    this.bomService.getProductforSerail(newDataList).subscribe(result=> {
      var dataList = JSON.parse(result["_body"]);
      //console.log(dataList)
      this.dataLists.push(dataList)
 console.log(this.dataLists)
      this.nextToFinish.emit(this.dataLists)
      this.dataList = {}
    })
  }

  changeValueUnit(value){
  this.dataList.Unit = value;
  }
}



