import { NgModule, ModuleWithProviders } from '@angular/core';
import { CommonModule } from '@angular/common';
import { HttpModule, Http } from '@angular/http';
import { RouterModule } from '@angular/router';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';

import { ModalModule as Angular2ModalModule } from 'angular2-modal';
import { BootstrapModalModule as Angular2BootstrapModalModule } from 'angular2-modal/plugins/bootstrap';
import { AccordionModule, BsDropdownModule, CarouselModule, DatepickerModule, ModalModule, TabsModule, TimepickerModule } from 'ngx-bootstrap';

 
 
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';


import { AuthService } from '../auth/shared/auth.service';
import { AuthguardGuard } from '../shared/guard/authguard.guard';

import { FilterManufacturerNamePipe } from './pipe/filter-manufacturerName.pipe';
import { FilterProductNamePipe } from './pipe/filter-productName.pipe';
import { FilterParameterValue } from './pipe/filter-parameterValue.pipe';

@NgModule({
  imports: [
    CommonModule,
    FormsModule,


    Angular2ModalModule.forRoot(),
    Angular2BootstrapModalModule,


    AccordionModule.forRoot(),
    BsDropdownModule.forRoot(),
    CarouselModule.forRoot(),
    DatepickerModule.forRoot(),
    ModalModule.forRoot(),
    TabsModule.forRoot(),
    TimepickerModule.forRoot(),
    BrowserAnimationsModule,


  ],
  declarations: [
 
    FilterManufacturerNamePipe,
    FilterProductNamePipe,
    FilterParameterValue
  ],
  exports: [
    CommonModule,
    HttpModule,
    RouterModule,
    FormsModule,
    ReactiveFormsModule,
    Angular2ModalModule,
    AccordionModule,
    BsDropdownModule,
    CarouselModule,
    DatepickerModule,
    ModalModule,
    TabsModule,
    TimepickerModule,
 
    FilterManufacturerNamePipe,
    FilterProductNamePipe,
    FilterParameterValue

  ],
})
export class SharedModule {
  static forRoot(): ModuleWithProviders {
    return {
      ngModule: SharedModule,
      providers: [       
        AuthService,
        AuthguardGuard
      ]
    };
  }
}



