import { Component, OnInit, Input, Output, EventEmitter, ViewChild } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import * as _ from 'lodash';
import * as moment from 'moment';
import { TrackService } from './shared/track.service';


@Component({
  selector: 'app-track',
  templateUrl: './track.component.html',
  styleUrls: ['./track.component.css']
})
export class TrackComponent implements OnInit {
  dataLists: Array<any>;
  AllSearchProduct: Array<any>;
  AllSearchManu: Array<any>;
  filterInvUIDByProduct: any;
  AllSearchProductAndManu: any;
  NewAllSearchManu: any;
  data: any;
  constructor(
    private trackService: TrackService
  ) { }

  unitName = {
    4: '斤'
  };

  ngOnInit() {
    this.trackService.getAllSearchProduct().subscribe(result => {
      var productList = [];
      console.log(result)
      _.map(result, O => { 
       if(_.findIndex(productList, { productName: O.productName })<0){
          productList.push({ productName:  O.productName })
         }     
      })
      this.AllSearchProduct = productList;
      this.AllSearchProductAndManu = result; 
    })
  /*   this.trackService.getAllSearchManu().subscribe(result => {
      this.AllSearchManu = result;
      console.log(result)
    }) */
    this.data = {}
  }

  select(type, value ) {
    this.data = _.assign({}, this.data, {
      [type]: value
    });
    
    if(type === 'ProductName') {
      this.data.ManufacturerName = null;
     this.NewAllSearchManu = _.filter(this.AllSearchProductAndManu,O => O.productName === value);
    }
  }

  send(){
    this.trackService.GetProductTrace(this.data).subscribe(result=> {
      this.dataLists = result
    });
   
  }

  
}



