import { Component, OnInit, Input, Output, EventEmitter, ViewChild } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import * as _ from 'lodash';
import * as moment from 'moment';

@Component({
  selector: 'app-stockout-all',
  templateUrl: './stockout-all.component.html',
  styleUrls: ['./stockout-all.component.css']
})
export class StockoutAllComponent implements OnInit {
  @Input() OutInventoryNo: any;
  @Input() AllSearchOrganization: any;
  @Input() unitLists: any;
  @Input() dataLists: any;
  @Input() AllSearchProduct: any;
  @Output() create = new EventEmitter();
  @Output() doDel = new EventEmitter();
  parameterValueByUnits: any;
  data: any;
  constructor() { }
  unitName = {
    斤: 4
  };
  ngOnInit() {
    this.data = {}
  
  }
  select(type, value) {
    this.data = _.assign({}, this.data, {
      [type]: value
    });
    if(type === 'unit') {
      this.parameterValueByUnits = _.filter(this.AllSearchProduct,O => O.parameterValue === value)[0].units;
     }
     if(type === 'ProductName') {
       
      this.data.unit = _.filter(this.AllSearchProduct,O => O.productName === value)[0].parameterValue;
     }
  }
  del(value) {
    this.doDel.emit(value)
  }
  add() {
    this.data['unit'] = this.parameterValueByUnits;
    console.log(this.data)
    this.create.emit(this.data);
    this.data = {}
  }

  acceptanceSerail(stockMID) {
    var no = stockMID.substring(0, 12);
    var acceptanceSerail = parseInt(stockMID.substring(13))

    if (acceptanceSerail < 9) {
      return no + "00" + acceptanceSerail
    } else {
      if (acceptanceSerail < 99) {
        return no + "0" + acceptanceSerail
      } else {
        return no + acceptanceSerail
      }
    }

  }

 
  
}



