import { Component, OnInit, Input, Output, EventEmitter, ViewChild } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { ExamineService } from '../shared/examine.service';
import * as _ from 'lodash';
import * as moment from 'moment';

@Component({
  selector: 'app-examine-all',
  templateUrl: './examine-all.component.html',
  styleUrls: ['./examine-all.component.css']
})
export class ExamineAllComponent implements OnInit {
  @Input() AcceptanceNo: any;
  @Input() AllSearchProduct: Array<any>;
  @Input() AllSearchManu: Array<any>;
  @Input() unitLists: Array<any>;
  @Input() dataLists: any;
  @Output() createAcceptance = new EventEmitter();
  @Output() changePage = new EventEmitter();
  @Output() changeDataListValue = new EventEmitter();
  @Output() delDataList = new EventEmitter();
  @Output() examine = new EventEmitter();

  constructor(
    private examineService: ExamineService
  ) {}

  ngOnInit() {
 
    
  }
  
  //examine-all-action
  create(data) {
    this.createAcceptance.emit(data);
  }
   

  //examine-all-list
  //換頁
  doChangePage(goPage) {
    this.changePage.emit(goPage);
  }
  // 回傳清單點選的該筆資料
  doExamine(acceptanceData: any) {
    this.examine.emit(acceptanceData);
  }

  doChangeDataListValue(dataList) {
   this.changeDataListValue.emit(dataList)
  }

  doDelDataList(data) {
    this.delDataList.emit(data)
  }
}
 


