import { Pipe, PipeTransform } from '@angular/core';
import * as _ from 'lodash';
import { TrackService } from '../shared/track.service';

@Pipe({
    name: 'filterInvUIDByProduct'
})

export class FilterInvUIDByProductPipe implements PipeTransform {
    AllSearchManu: any;
    constructor(
        private trackService: TrackService
    ) { }

    transform(dataLists: Array<any>, ManufacturerName: string): any {
        this.trackService.getAllSearchManu().subscribe(result => {
            this.AllSearchManu = result;

        })

        var findId = _.find(this.AllSearchManu, { manufacturerName: ManufacturerName })

        if (findId) {
            return _.filter(dataLists, { invUID: findId.id })

        }
        return dataLists
    }
}


