import { Component, OnInit } from '@angular/core';
import { StockoutService } from './shared/stockout.service';
import * as _ from 'lodash';
import * as moment from 'moment';

@Component({
  selector: 'app-stockout',
  templateUrl: './stockout.component.html',
  styleUrls: ['./stockout.component.css']
})
export class StockoutComponent implements OnInit {
  OutInventoryNo: string
  dataLists: Array<any>
  dataList: any
  AllSearchProduct: Array<any>;
  AllSearchOrganization: Array<any>;
  currentPage: number
  data: any;
  index: number = 0;
  unitLists: any;
  constructor(
    private stockoutService: StockoutService
  ) { }

  ngOnInit() {
    this.currentPage = 1;
    this.stockoutService.NewOutInventory().subscribe(result => {
      this.OutInventoryNo = result;
    })
    this.stockoutService.getAllSearchProduct().subscribe(result => {
      var unitLists = [];
      this.AllSearchProduct = result;
      _.map(result, O => {
        var a = _.findIndex(unitLists, { 'parameterValue': O.parameterValue });
        if (a < 0) {
           unitLists.push({ parameterValue: O.parameterValue,
            units: O.units })
        }
      })
      this.unitLists = unitLists;
    })

    this.stockoutService.AllSearchOrganization().subscribe(result => {
      this.AllSearchOrganization = result;
    })
  }

  create(data) {
    this.data = data;
    var a = _.findIndex(this.dataLists, { 'productName': data.ProductName })
     
 

    if (a < 0) {
      
    }else {
      var quantity = data.outInventoryQuantity + this.dataLists[a].quantity;
      data = _.assign({}, this.data, {
        outInventoryQuantity: quantity
      })
    }  


    this.stockoutService.GetOutInventory(data).subscribe(result => {
      this.index = this.index + 1;
      var dataList = _.assign({}, JSON.parse(result["_body"]), {
        index: this.index
      });;
      if (result.ok) {
        
        if (this.dataLists === undefined) {
          this.dataLists = [];
        }
        /*         var dataLists = this.dataLists.filter((dataList) => dataList.productName != data.ProductName);
                dataList.quantity = dataList.quantity + data.outInventoryQuantity */

        /*  var a = _.findIndex(this.dataLists, { 'productName': data.ProductName });
         if (a < 0) {
           this.dataLists.push(dataList);
         }else {
           dataList.quantity = dataList.quantity + this.dataLists[a].quantity;
           console.log(dataList)
           this.dataLists.push(dataList);
         } */

         this.dataLists = this.dataLists.filter((dataList) => dataList.productName != data.ProductName);

        this.dataLists.push(dataList);
       
      }
    })

  }

  doDel(data) {
    var dataLists = this.dataLists.filter((dataList) => dataList.index != data.index);
    this.dataLists = dataLists
  }

  send() {
   this.dataLists = this.dataLists.filter((dataList) => dataList.batchs != null);
    if(this.dataLists[0]) {
      this.stockoutService.addOutInventory({ Product: this.dataLists, InInventoryOrganizationName: this.data.SearchOrganization, No: this.OutInventoryNo }).subscribe(result => {
        if (result.ok) {
          this.dataLists = JSON.parse(result["_body"]);
          this.currentPage = 2
        }
      })
    }else {
      console.log("DataLists is Null !!")
    }
     
  }

  goNewPage() {
    this.currentPage = 1
    this.stockoutService.NewOutInventory().subscribe(result => {
      this.OutInventoryNo = result;

    })
    this.dataLists = [];
  }

}



