import { Component, OnInit, Input, Output, EventEmitter, ViewChild } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import * as _ from 'lodash';
import * as moment from 'moment';
import * as qrcode from 'qrcode';

@Component({
  selector: 'app-stockout-print',
  templateUrl: './stockout-print.component.html',
  styleUrls: ['./stockout-print.component.css']
})
export class StockoutPrintComponent implements OnInit {
  @Input() OutInventoryNo: any;
  @Input() dataLists: any;
  dataUrl: any;
  constructor() { }

  ngOnInit() {
   
    qrcode.toDataURL("/menu/inventoryList/"+this.OutInventoryNo, (err, dataUrl) => {
      this.dataUrl = dataUrl;
    });
    console.log(this.dataLists)
  }

  acceptanceSerail(stockMID) {
    var no = stockMID.substring(0, 12);
    var acceptanceSerail = parseInt(stockMID.substring(13))

    if (acceptanceSerail < 9) {
      return no + "00" + acceptanceSerail
    } else {
      if (acceptanceSerail < 99) {
        return no + "0" + acceptanceSerail
      } else {
        return no + acceptanceSerail
      }
    }
  }
  

}



