import { Injectable } from '@angular/core';
import { Http, Response, Headers, RequestOptions } from "@angular/http";
import 'rxjs/add/operator/map';

@Injectable()
export class TrackService {
  url = '/YCYF/GetProductTraces?ProductName=';
  url2 = '/YCYF/GetOutInventory';
  url5 = 'YCYF/AllSearchManu';
  url6 = 'YCYF/AllSearchManufacturers';
  constructor(
    private http: Http
  ) { }



  GetProductTrace(data) {
    return this.http.get(this.url + data.ProductName +"&ManufacturerName="+ data.ManufacturerName)
      .map((response: Response) => response.json());
  }

  getAllSearchProduct() {
    return this.http.get(this.url5)
      .map((response: Response) => response.json());
  }

  getAllSearchManu() {
    return this.http.get(this.url6)
      .map((response: Response) => response.json());
  }

}




